import { defineStore } from "pinia";

export const usePagingStore = defineStore('paging', {
    state: () => ({
        pageNum: 1,
        amount: 10,
        total: 172,
    }),

    getters: {
        endPage: (state) => {
            const calculatedEndPage = Math.ceil(state.pageNum / 10.0) * 10;
            return Math.min(calculatedEndPage, state.realEnd);
        },
        startPage: (state) => {
            return state.endPage - 9;
        },
        realEnd: (state) => {
            return Math.ceil(state.total * 1.0 / state.amount);
        },
        prev: (state) => {
            return state.startPage > 1;
        },
        next: (state) => {
            return state.endPage < state.realEnd;
        }
    } ,



});
